body {
  box-sizing: border-box;
  background-color: hsla(202, 30%, 7%, 0.849);
}

.card {
  display: flex;
  width: 30%;
  padding: 2rem;
  margin: 2rem auto auto auto;
  /* border: 3px solid black; */
  border-radius: 12px;
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.5);
}
h3 {
  margin: auto;
  font-size: 2rem;
}

img {
  width: 6rem;
  height: 6rem;
  padding: 1rem;
  box-shadow: -2px -2px 20px 2px black, 5px 5px 30px 5px blue;
  border-radius: 12px;
  margin: auto;
}
